<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td><code v-pre>'.'</code></td>
<td><code v-pre>optional</code> 指定图片服务地址。</td>
</tr>
<tr>
<td>fileExtension</td>
<td>String</td>
<td><code v-pre>'png'</code></td>
<td><code v-pre>optional</code> 指定图片服务影像扩展名。</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> 指定服务版权描述信息。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定服务最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定服务最大层级。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定影像加载的矩形范围。</td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定服务坐标系参数。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定参考椭球体。默认 WGS84 椭球体。</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> 指定图像瓦片宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> 指定图像瓦片高度。</td>
</tr>
<tr>
<td>flipXY</td>
<td>Boolean</td>
<td></td>
<td><code v-pre>optional</code> 较旧版本的gdal2tiles.py在tilemapresource.xml中翻转了X和Y值。 指定此选项将执行相同操作，允许加载这些不正确的tileset。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/TileMapServiceImageryProvider.html">TileMapServiceImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者准备好时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          alpha: 1,
          brightness: 1,
          contrast: 1,
          url: './statics/SampleData/images/cesium_maptiler/Cesium_Logo_Color',
          rectangle: { west: -120, south: 20, east: -60, north: 40 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.viewer = viewer
          // ...
        },
        imageryReady(imageryProvider) {
          this.viewer.camera.flyTo({ destination: imageryProvider.rectangle })
        }
      }
    }
  </script>